"use client";
import { useState } from "react";
import Header from "../components/Header";

export default function CreateTaskPage() {
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [pending, setPending] = useState(true);
  const token = localStorage.getItem("token");

  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("http://localhost:5000/api/alltasks", {
        method: "POST",
        headers: { "Content-Type": "application/json", Authorization: `Bearer ${token}` },
        body: JSON.stringify({ title, description, pending }),
      });
      if (!res.ok) throw new Error("Failed");
      window.location.href = "/alltasks"; // Redirect to AllTasks after creation
    } catch {
      console.error("Cannot reach backend!");
    }
  };

  return (
    <div className="min-h-screen p-6 bg-sky-200">
      <Header />
      <form onSubmit={handleCreate} className="card max-w-md mx-auto flex flex-col gap-4">
        <h2 className="text-2xl font-bold text-center text-purple-700">Create Task</h2>
        <input placeholder="Title" value={title} onChange={e => setTitle(e.target.value)} className="border p-2 rounded" required />
        <textarea placeholder="Description" value={description} onChange={e => setDescription(e.target.value)} className="border p-2 rounded" rows={4} required />
        <label className="flex items-center gap-2">
          <input type="checkbox" checked={!pending} onChange={() => setPending(!pending)} />
          <span>Mark as Completed</span>
        </label>
        <button type="submit" className="btn-green w-full">Create Task</button>
      </form>
    </div>
  );
}
